'use client'

import { useState } from 'react'

interface FormData {
  name: string
  email: string
  message: string
}

interface FormErrors {
  name?: string
  email?: string
  message?: string
}

export default function ContactForm() {
  const [formData, setFormData] = useState<FormData>({
    name: '',
    email: '',
    message: ''
  })
  
  const [errors, setErrors] = useState<FormErrors>({})
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [isSubmitted, setIsSubmitted] = useState(false)

  const validateEmail = (email: string): boolean => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    return emailRegex.test(email)
  }

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {}

    if (!formData.name.trim()) {
      newErrors.name = 'Name is required'
    }

    if (!formData.email.trim()) {
      newErrors.email = 'Email is required'
    } else if (!validateEmail(formData.email)) {
      newErrors.email = 'Please enter a valid email address'
    }

    if (!formData.message.trim()) {
      newErrors.message = 'Message is required'
    } else if (formData.message.trim().length < 10) {
      newErrors.message = 'Message must be at least 10 characters long'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
    
    // Clear error when user starts typing
    if (errors[name as keyof FormErrors]) {
      setErrors(prev => ({
        ...prev,
        [name]: undefined
      }))
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) {
      return
    }

    setIsSubmitting(true)

    try {
      // Simulate form submission
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      console.log('Form submitted:', formData)
      setIsSubmitted(true)
      setFormData({ name: '', email: '', message: '' })
      
      // Reset success message after 5 seconds
      setTimeout(() => {
        setIsSubmitted(false)
      }, 5000)
      
    } catch (error) {
      console.error('Form submission error:', error)
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <section className="py-20 bg-charcoal">
      <div className="container-portfolio">
        <div className="max-w-4xl mx-auto">
          <div className="text-center mb-16 fade-in">
            <h2 className="text-4xl lg:text-5xl font-bold text-white mb-6">
              Contact Me
            </h2>
            <p className="text-xl text-gray-300 max-w-2xl mx-auto">
              Ready to start your next creative project? Let's discuss how we can 
              bring your vision to life through compelling animation and design.
            </p>
          </div>

          <div className="grid lg:grid-cols-2 gap-16">
            {/* Contact Information */}
            <div className="fade-in">
              <h3 className="text-2xl font-bold text-white mb-8">
                Let's Connect
              </h3>
              
              <div className="space-y-6">
                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-gold rounded-full flex items-center justify-center flex-shrink-0 mt-1">
                    <span className="text-charcoal font-bold">E</span>
                  </div>
                  <div>
                    <div className="text-gold font-semibold mb-1">Email</div>
                    <div className="text-gray-300">hello@christianjungco.com</div>
                  </div>
                </div>

                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-white rounded-full flex items-center justify-center flex-shrink-0 mt-1">
                    <span className="text-charcoal font-bold">P</span>
                  </div>
                  <div>
                    <div className="text-gold font-semibold mb-1">Phone</div>
                    <div className="text-gray-300">+1 (555) 123-4567</div>
                  </div>
                </div>

                <div className="flex items-start space-x-4">
                  <div className="w-12 h-12 bg-gold rounded-full flex items-center justify-center flex-shrink-0 mt-1">
                    <span className="text-charcoal font-bold">L</span>
                  </div>
                  <div>
                    <div className="text-gold font-semibold mb-1">Location</div>
                    <div className="text-gray-300">Los Angeles, CA</div>
                  </div>
                </div>
              </div>

              <div className="mt-12">
                <h4 className="text-xl font-semibold text-white mb-4">
                  Follow My Work
                </h4>
                <div className="flex space-x-4">
                  <div className="w-12 h-12 bg-gray-800 rounded-full flex items-center justify-center hover:bg-gold hover:text-charcoal transition-colors duration-300 cursor-pointer">
                    <span className="font-bold">Li</span>
                  </div>
                  <div className="w-12 h-12 bg-gray-800 rounded-full flex items-center justify-center hover:bg-gold hover:text-charcoal transition-colors duration-300 cursor-pointer">
                    <span className="font-bold">Be</span>
                  </div>
                  <div className="w-12 h-12 bg-gray-800 rounded-full flex items-center justify-center hover:bg-gold hover:text-charcoal transition-colors duration-300 cursor-pointer">
                    <span className="font-bold">Dr</span>
                  </div>
                  <div className="w-12 h-12 bg-gray-800 rounded-full flex items-center justify-center hover:bg-gold hover:text-charcoal transition-colors duration-300 cursor-pointer">
                    <span className="font-bold">Ig</span>
                  </div>
                </div>
              </div>
            </div>

            {/* Contact Form */}
            <div className="fade-in">
              {isSubmitted && (
                <div className="mb-6 p-4 bg-green-900 border border-green-700 rounded-lg">
                  <div className="text-green-300 font-semibold">
                    Thank you for your message! I'll get back to you soon.
                  </div>
                </div>
              )}

              <form onSubmit={handleSubmit} className="space-y-6">
                <div>
                  <label htmlFor="name" className="block text-white font-semibold mb-2">
                    Name *
                  </label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 bg-gray-900 border rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 transition-colors duration-300 ${
                      errors.name 
                        ? 'border-red-500 focus:ring-red-500' 
                        : 'border-gray-700 focus:ring-gold focus:border-gold'
                    }`}
                    placeholder="Your full name"
                  />
                  {errors.name && (
                    <div className="mt-1 text-red-400 text-sm">{errors.name}</div>
                  )}
                </div>

                <div>
                  <label htmlFor="email" className="block text-white font-semibold mb-2">
                    Email *
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 bg-gray-900 border rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 transition-colors duration-300 ${
                      errors.email 
                        ? 'border-red-500 focus:ring-red-500' 
                        : 'border-gray-700 focus:ring-gold focus:border-gold'
                    }`}
                    placeholder="your.email@example.com"
                  />
                  {errors.email && (
                    <div className="mt-1 text-red-400 text-sm">{errors.email}</div>
                  )}
                </div>

                <div>
                  <label htmlFor="message" className="block text-white font-semibold mb-2">
                    Message *
                  </label>
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleInputChange}
                    rows={6}
                    className={`w-full px-4 py-3 bg-gray-900 border rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 transition-colors duration-300 resize-vertical ${
                      errors.message 
                        ? 'border-red-500 focus:ring-red-500' 
                        : 'border-gray-700 focus:ring-gold focus:border-gold'
                    }`}
                    placeholder="Tell me about your project, goals, and how I can help bring your vision to life..."
                  />
                  {errors.message && (
                    <div className="mt-1 text-red-400 text-sm">{errors.message}</div>
                  )}
                </div>

                <button
                  type="submit"
                  disabled={isSubmitting}
                  className={`w-full py-4 font-semibold rounded-lg transition-all duration-300 ${
                    isSubmitting
                      ? 'bg-gray-600 text-gray-400 cursor-not-allowed'
                      : 'bg-gold text-charcoal hover:bg-opacity-90 hover:scale-105'
                  }`}
                >
                  {isSubmitting ? 'Sending Message...' : 'Send Message'}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
