'use client'

import { useState, useEffect } from 'react'

export default function Testimonials() {
  const [currentIndex, setCurrentIndex] = useState(0)

  const testimonials = [
    {
      id: 1,
      quote: "Christian's creative vision and attention to detail transformed our brand completely. His animations brought our story to life in ways we never imagined possible.",
      name: "Sarah Johnson",
      title: "CEO, TechFlow Solutions",
      company: "TechFlow"
    },
    {
      id: 2,
      quote: "Working with Christian was an absolute game-changer. His art direction elevated our campaign and the results exceeded all our expectations.",
      name: "Michael Chen",
      title: "Marketing Director, InnovateCorp",
      company: "InnovateCorp"
    },
    {
      id: 3,
      quote: "The brand identity Christian created for us perfectly captures our essence. His strategic approach to brand building is truly exceptional.",
      name: "Emily Rodriguez",
      title: "Founder, Creative Studios",
      company: "Creative Studios"
    },
    {
      id: 4,
      quote: "Christian's motion graphics work is simply outstanding. He has an incredible ability to translate complex ideas into engaging visual narratives.",
      name: "David Thompson",
      title: "Creative Lead, MediaWorks",
      company: "MediaWorks"
    },
    {
      id: 5,
      quote: "Professional, creative, and results-driven. Christian delivered beyond our expectations and helped establish our brand in the market.",
      name: "Lisa Park",
      title: "Brand Manager, StartupX",
      company: "StartupX"
    }
  ]

  // Auto-rotate testimonials
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) => 
        prevIndex === testimonials.length - 1 ? 0 : prevIndex + 1
      )
    }, 5000)

    return () => clearInterval(interval)
  }, [testimonials.length])

  const goToPrevious = () => {
    setCurrentIndex(currentIndex === 0 ? testimonials.length - 1 : currentIndex - 1)
  }

  const goToNext = () => {
    setCurrentIndex(currentIndex === testimonials.length - 1 ? 0 : currentIndex + 1)
  }

  const goToSlide = (index: number) => {
    setCurrentIndex(index)
  }

  return (
    <section className="py-20 bg-gray-900">
      <div className="container-portfolio">
        <div className="text-center mb-16 fade-in">
          <h2 className="text-4xl lg:text-5xl font-bold text-white mb-6">
            Client Testimonials
          </h2>
          <p className="text-xl text-gray-300 max-w-2xl mx-auto">
            Hear what clients say about working with me and the impact of our 
            creative collaborations on their brands.
          </p>
        </div>

        <div className="max-w-4xl mx-auto">
          {/* Testimonial Content */}
          <div className="relative overflow-hidden">
            <div 
              className="flex transition-transform duration-500 ease-in-out"
              style={{ transform: `translateX(-${currentIndex * 100}%)` }}
            >
              {testimonials.map((testimonial) => (
                <div key={testimonial.id} className="w-full flex-shrink-0 px-4">
                  <div className="text-center">
                    <div className="mb-8">
                      <div className="text-6xl text-gold mb-6 opacity-50">"</div>
                      <blockquote className="text-2xl lg:text-3xl text-white leading-relaxed font-light italic">
                        {testimonial.quote}
                      </blockquote>
                    </div>
                    
                    <div className="space-y-2">
                      <div className="text-xl font-semibold text-gold">
                        {testimonial.name}
                      </div>
                      <div className="text-gray-300">
                        {testimonial.title}
                      </div>
                      <div className="text-gray-400 text-sm">
                        {testimonial.company}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Navigation Controls */}
          <div className="flex items-center justify-center mt-12 space-x-8">
            <button
              onClick={goToPrevious}
              className="text-gold hover:text-white transition-colors duration-300 font-semibold text-lg"
              aria-label="Previous testimonial"
            >
              Prev
            </button>

            {/* Dots Indicator */}
            <div className="flex space-x-3">
              {testimonials.map((_, index) => (
                <button
                  key={index}
                  onClick={() => goToSlide(index)}
                  className={`w-3 h-3 rounded-full transition-all duration-300 ${
                    index === currentIndex 
                      ? 'bg-gold scale-125' 
                      : 'bg-gray-600 hover:bg-gray-400'
                  }`}
                  aria-label={`Go to testimonial ${index + 1}`}
                />
              ))}
            </div>

            <button
              onClick={goToNext}
              className="text-gold hover:text-white transition-colors duration-300 font-semibold text-lg"
              aria-label="Next testimonial"
            >
              Next
            </button>
          </div>

          {/* Progress Bar */}
          <div className="mt-8 max-w-xs mx-auto">
            <div className="w-full bg-gray-700 rounded-full h-1">
              <div 
                className="bg-gold h-1 rounded-full transition-all duration-300"
                style={{ width: `${((currentIndex + 1) / testimonials.length) * 100}%` }}
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
